<template>
  <div class="addself">
    <div class="head">添加自选<span @click="back">&#xe6a8;</span></div>
    <div class="scheader">
      <span class="scheader1">名称代码</span><span class="scheader2">最新价</span><span class="scheader3"></span><span class="scheader4">添加</span>
    </div>
    <div ref="scroll" class="scroll">
      <div class="scrollB">
    <div class="item2" v-for="(item,index) in mainList" :key="index">
      <div class="item-name" @click="show">
        <span class="name">{{item.name}}</span>
        <span class="ename">{{item.instrumentid}}</span>
      </div>
      <div class="item-price">
        <span class="price1" :class="[item.diffPrice>0?'changeRed':(item.diffPrice<0?'changeGre':'')]">{{item.NEWPRICE}}{{item.diffPrice<0?'&#xe7d0;':(item.diffPrice>0?'&#xe7d2;':'')}}</span>
        <span class="price2" :class="[item.diffPrice>0?'changeRed':(item.diffPrice<0?'changeGre':'')]">{{item.diffPrice>0?item.diffPrice:item.diffPrice*(-1)}}/{{item.diffPriceRate}}</span>
      </div>
      <div class="item-choose" >
        <!-- -------------------------zlq--------------------------------------- -->
          <span :class="{'select':item.type != 0}" @click="changeoptional(item)"></span>
        <!-- -------------------------zlq--------------------------------------- -->
      </div>
    </div>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      instrumentId:'',
      uid:'',
      name:'',
      type:0,
      item:{}
    }
  },
  props:{
    mainList:{
      type:Object
    }
  },
  created(){
    this.uid = localStorage.getItem('uid');

    // -----------------------------zlq----------------------------------------
    // 将自选数据对应在mainList数据中添加属性
    this.getoptional();
    // -----------------------------zlq----------------------------------------
  },
  methods:{
    show(){
      console.log(this.mainList['S2001'])
    },
    back(){
      this.$router.go(-1)
    },

    // -----------------------------zlq----------------------------------------
    // 将自选数据对应在mainList数据中添加属性
    getoptional(){
      // 先判断用户是否有自选
      if(localStorage.getItem("optional")){
        // 有则，获取本地存储的自选数据。
        this.optional = JSON.parse(localStorage.getItem("optional"))
        
        for(let key in this.mainList){
          this.mainList[key].type = 1;
          for(let i = 0 ; i < this.optional.length; i++){
            if(this.mainList[key].name == this.optional[i]){
              this.mainList[key].type = 0;
            }
          }
        }
      }
    },

    // 增加自选或者选择自选
    changeoptional(item){
      // 0 ==> 用户已自选  1 ==> 用户未自选
      if(item.type == 0){
        // 获取用户自选数据
        let optional = JSON.parse(localStorage.getItem('optional'));
        
        // 过滤 ==> 将用户当前的期货从本地存储中删除掉。
        optional = optional.filter((val) => {
          return val != item.name;
        })

        // 先删除所有本地自选数据，再添加过滤后的数据存储在本地
        localStorage.removeItem('optional')
        localStorage.setItem("optional",JSON.stringify(optional));

        // 循环找到对应的期货，则删除并返回
        for(let key in this.mainList){
          if(this.mainList[key].name == item.name){
            this.mainList[key].type = 1;
            return
          }
        }
      }else if(item.type == 1){
        let optional = [];
        if(localStorage.getItem("optional")){
          // 有则，获取本地存储的自选数据。并添加新的自选数据
          optional = JSON.parse(localStorage.getItem("optional"))
          optional.push(item.name);
        }else{
          // 无则，直接添加
          optional.push(item.name);  
        }
        // 将自选数据存储在本地
        localStorage.setItem("optional",JSON.stringify(optional));

        // 循环找到对应的期货，则添加并返回
        for(let key in this.mainList){
          if(this.mainList[key].name == item.name){
            this.mainList[key].type = 0;
            return;
          }
        }
      }
    },
    // -----------------------------zlq----------------------------------------

    add(item){
      this.name=item.name;
      this.instrumentId=item.instrumentid;
      this.item=item;
      console.log(item)
      let type=0;
      if(item.type==0){
        type=1;
      }
      this.type=type;
      this.handdleMsg();
    },
    handdleMsg(msg) {
      let that = this;
      if (that.global.ws && that.global.ws.readyState == 1) {
        console.log("发送信息", msg);
        that.global.ws.onopen = that.onopen()
      }
      that.global.ws.onmessage = function(res) {
        console.log(res)
        // that.websocketonmessage (res);
        that.$emit('addSelf',{
          name:that.instrumentId,
          type:that.type
        })
        // that.$emit('repush');
        console.log(res)
      };
    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this;
      if (this.type==0) {
        actions = {                    //删除
          action: "deleteInstrumentOptional",
          reqID: 12,
          data: {
            instrumentId : that.instrumentId,
            userId : that.uid,
            instrumentName:that.name
          }
        }
      } else {                       //添加
        actions = {
          action: "insertInstrumentOptional",
          reqID: 13,
          data: {
            instrumentId : that.instrumentId,
            userId : that.uid,
            instrumentName:that.name
          },

        }
      }
      console.log(JSON.stringify(actions));
      that.global.ws.send(JSON.stringify(actions));
    },
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .addself {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    background-color: #FFF;
    padding-top: 28px;
    .head {
      width: 100vw;
      height: tovw(40);
      font-size: tovw(20);
      text-align: center;
      line-height: tovw(40);
      color: #000;
      position: relative;
      &>span {
        font-family: 'iconfont';
        display: inline-block;
        position: absolute;
        left: tovw(18);
        top: 0;
        line-height: tovw(40);
        font-size: tovw(20);
        color: #000;
      }
    }
    .scheader {
      height: tovw(30);
      font-size: tovw(14);
      color: #8C8B8B;
      border-top: tovw(1) solid #D5D3D3;
      text-align: left;
      background-color: #FFF;
      span {
        display: inline-block;
      }
      .scheader1 {
        /*width: tovw(154);*/
        width: tovw(120);
        padding-left: tovw(16);
        /*padding-right: tovw(16);*/
        margin-right: tovw(25);
        line-height: tovw(30);
        text-align: center;
      }
      .scheader2 {
        margin-right:tovw(43);
        line-height: tovw(30);
        width: tovw(78);
        text-align: right;
      }
      .scheader3 {
        font-family: 'iconfont'!important;
        width: tovw(50);
        text-align: center;
      }
      .scheader4 {
        font-family: 'iconfont'!important;
      }
    }
    .scroll {
      /*height: tovw(506);*/
      position: absolute;
      width: 100%;
      top: tovw(70);
      bottom: tovw(50);
      overflow: auto;
    }
    .scroll::-webkit-scrollbar {
      /*display: none;*/
      width:1px;
      background-color: rgba(225,225,225,0);
    }
    .item2 {
      width: 100%;
      height: tovw(50);
      background-color: #FFFFFF;
      border-bottom: tovw(1) solid #f4f4f4;
      padding: tovw(4)  tovw(0) tovw(4) tovw(16);
      font-size: 0;
      position: relative;
      span {
        display: block;
      }
      .item-name {
        display: inline-block;
        /*width: tovw(138);*/
        /*margin-right: tovw(35);*/
        width: tovw(94);
        text-align: center;
        .name {
          font-size: tovw(16);
          color: #4F4F4F;
          /*font-weight: 700;*/
        }
        .ename {
          font-size: tovw(10);
          color: #8C8B8B;
        }
      }
      .item-price {
        display: inline-block;
        color: #000;
        width: tovw(117);
        text-align: right;
        /*padding-right: tovw(74);*/
        .price1 {
          font-size: tovw(16);
          font-family: 'iconfont'!important;
          /*font-weight: 600;*/
          &.changeRed {
            color:#FD4F50;
          }
          &.changeGre {
            color:#0CAD6B;
          }
        }
        .price2 {
          font-size: tovw(11);
          &.changeRed {
            color:#FD4F50;
          }
          &.changeGre {
            color:#0CAD6B;
          }
        }
      }
      .item-choose{
        display: inline-block;
        color: #000;
        width: tovw(130);
        text-align: right;
        height: tovw(37);
        line-height: tovw(37);
        position: relative;
        &>span {
          width: tovw(25);
          height: tovw(25);
          display: inline-block;
          position: absolute;
          top:tovw(6);
          right:tovw(15);
          transition: all 0.1s;
          background: no-repeat;
          @media (-webkit-min-device-pixel-ratio: 1){
            background-image: url("../../assets/icon/ok.png");
          }
          @media (-webkit-min-device-pixel-ratio: 2){
            background-image: url("../../assets/icon/ok@2x.png");
          }
          @media (-webkit-min-device-pixel-ratio: 3){
            background-image: url("../../assets/icon/ok@3x.png");
          }
          &.select {
            @media (-webkit-min-device-pixel-ratio: 1){
              background-image: url("../../assets/icon/add.png");
            }
            @media (-webkit-min-device-pixel-ratio: 2){
              background-image: url("../../assets/icon/add@2x.png");
            }
            @media (-webkit-min-device-pixel-ratio: 3){
              background-image: url("../../assets/icon/add@3x.png");
            }
          }
          background-size: tovw(25) tovw(25);
        }
      }
    }
  }
</style>



